@import url('https://rsms.me/inter/inter.css');

html,
body,
#root {
  position: relative;
  margin: 0;
  padding: 0;
  overflow: hidden;
  outline: none;
  width: 100vw;
  height: 100vh;
}

body {
  background: rgb(231, 230, 230);
  font-family: 'Inter var', sans-serif;
  display: flex;
  justify-content: center;
}

#root {
  width: 76ch;
}

.picker {
  position: absolute !important;
  top: 25px;
  left: 25px;
  width: 100px !important;
  height: 100px !important;
}
